import './AdminTipoVehiculo.css'

import { observer } from 'mobx-react-lite'
import React from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { RUTAS, TIPOS_REFRIGERACION_STR } from '../../constants'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const numeral = require('numeral')

export const AdminTipoVehiculo: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()
  const addToast = useToaster()

  const editar = (row: any) => {
    history.push(RUTAS.ADMIN_TIPO_VEHICULO_UPDATE(row.id))
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    const dtable = DataTable.current
    rootStore.environment.api.eliminarTipoVehiculo(row.id).then((result: any) => {
      if (result.kind === 'ok') {
        addToast('Eliminado satisfactoriamente', 'success')
        dtable.refresh()
      } else {
        addToast('Ocurrió un error al eliminar vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Editar'} onClick={() => editar(row)}>
          <FIcon.Edit size={14} />
        </span>
        <span className={'button ml-3'} title={'Eliminar'} onClick={() => eliminar(row)}>
          <FIcon.Trash size={14} />
        </span>
      </div>
    )
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Tipos de vehículo</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Tipos de vehículo</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button
                className="btn btn-primary d-flex align-items-center px-3"
                id="add-tipo-vehiculo"
                onClick={() => {
                  history.push(RUTAS.ADMIN_TIPO_VEHICULO_CREATE)
                }}
              >
                Crear Tipo de Vehículo
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                obtenerDatos={(perPage: number, page: number, order: any) => {
                  return rootStore.environment.api.tiposVehiculo(page, perPage, order)
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Subida de bandera',
                    selector: 'subida_bandera',
                    sortable: true,
                    cell: (row: any) => numeral(row.subida_bandera).format('0,0'),
                  },
                  {
                    name: 'Precio mínimo',
                    selector: 'precio_minimo',
                    sortable: true,
                    cell: (row: any) => numeral(row.precio_minimo).format('0,0'),
                  },
                  {
                    name: 'Precio por kilómetro',
                    selector: 'precio_km',
                    sortable: true,
                    cell: (row: any) => numeral(row.precio_km).format('0,0'),
                  },
                  {
                    name: 'Precio por minuto',
                    selector: 'precio_minuto',
                    sortable: true,
                    cell: (row: any) => numeral(row.precio_minuto).format('0,0'),
                  },
                  {
                    name: 'Volumen en m3',
                    selector: 'espacio_volumen_m3',
                    sortable: true,
                    cell: (row: any) => numeral(row.espacio_volumen_m3).format('0,0.000'),
                  },
                  {
                    name: 'Refrigeración',
                    selector: 'refrigerado',
                    sortable: true,
                    cell: (row: any) => {
                      // @ts-ignore
                      return TIPOS_REFRIGERACION_STR[row.refrigeracion]
                    },
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminTipoVehiculo
